<template>
  <div class="wrap">
    <div class="contents_wrap">
      <div class="main_tit">
        <div class="logo"><img src="../assets/img/logo_paprica.png" /></div>
        <div class="app_tit">파프리카 결제 시스템</div>
      </div>

      <div class="login_form">
        <div class="login_wrap">
          <div class="input_con_wrap">
            <div for="userId" class="input_tit">아이디</div>
            <input
              type="text"
              placeholder="아이디를 입력해주세요."
              v-model="userId"
            />
          </div>
          <div class="input_con_wrap">
            <div for="userPassword" class="input_tit">비밀번호</div>
            <input
              type="password"
              placeholder="비밀번호를 입력해주세요."
              v-model="userPassword"
            />
          </div>
        </div>
        <button class="login_btn" type="submit" @click="submitForm">
          로그인
        </button>
      </div>

      <div class="company">(주)더포스웨이브</div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data: function () {
    return {
      userId: "",
      userPassword: "",
      // access_token: "",
    };
  },
  methods: {
    submitForm: function () {
      console.log(this.userId, this.userPassword);

      var url = "https://account.api.paprica.fit/v1/account/authenticate";
      var data = {
        email: this.userId,
        password: this.userPassword,
      };

      axios
        .post(url, data)
        .then((response) => {
          alert(response.data.code);
          if (response.data.code == "SUCCESS") {
            this.$store.commit(
              "setUsername",
              response.data.account_main[0].email
            );
            this.$store.commit("setToken", response.data.data.access_token);
            // this.$store.dispatch("login", {
            //   email: this.adminId,
            //   password: this.adminPw,
            // });
            // this.$store.commit("aid", response.data.data.account_main[0].aid);

            // access token 값 저장 1. store 2. local storage 3. cookie
            // alert(response.data.data.access_token);

            // const access_token = response.data.data.access_token;
            // console.log("AT", access_token);

            // const aid = response.data.data.account_main[0].aid;
            // console.log("aid", aid);

            console.log("successe", response.data);
            // document.location.href("./CardList.vue");
          } else {
            console.log("fail", response);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
};
</script>

<style>
.wrap {
  width: 100%;
  max-width: 1024px;
  height: 100vh;
  margin: 0 auto;
  display: inline-block;
}

.contents_wrap {
  margin: auto 24px;
}
/** login **/
.main_tit {
  margin: 32px 0 0;
}
.main_tit .logo {
  text-align: center;
}
.main_tit .app_tit {
  font-size: 14px;
  color: #fff;
  text-align: center;
  margin-top: 10px;
}
.login_form {
  margin: auto 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: calc(100% - 48px);
}
button.login_btn {
  margin-top: 16px;
}
.company {
  position: absolute;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 14px;
  color: #868e96;
  font-weight: 600;
}
</style>
